export default {
	data(){
		return {
			imgArr: []
		}
	},
	methods: {
		chooseImg () {
			uni.chooseImage({
				count: 5,
				success:res=>{
					this.imgArr = res.tempFilePaths
				}
			})
		},
		
		previewImg (current) {
			uni.previewImage({
				current,
				urls: this.imgArr,
				loop: true,
				indicator: 'default'
			})
		}
	},
  onLoad () {




		console.log('我希望微信小程序中打印')

	}
}
